/* HEX  KUSMIS colors */

.grayscale {
  filter: grayscale(100%);
  transition: 1s;
}

.grayscale:hover {
  filter: grayscale(0);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  box-shadow: 0 0 0 transparent;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  position: relative;
  font-size: 1.125rem;
}

[disabled="disabled"] {
  cursor: default !important;
}

b,
strong,
.strong {
  font-weight: 600;
}

.stronger {
  font-weight: 700;
}

h1,
.h1 {
  color: #232323;
  font-family: "Source Code Pro", monospace;
  font-weight: 100;
  text-transform: uppercase;
}

h1,
.h1 {
  margin-bottom: 3rem;
  font-size: 3rem;
}

h1 + .lead,
.h1 + .lead {
  margin-top: -2.4rem;
}

h2,
.h2 {
  margin-bottom: 2.5rem;
  font-size: 1.5rem;
  font-weight: 600;
}

h3,
.h3 {
  margin-bottom: 2.5rem;
  font-size: 1.3125rem;
  font-weight: 600;
}

h4,
.h4 {
  margin-bottom: 2rem;
  font-size: 1.125rem;
  font-weight: 600;
}

h5,
.h5 {
  margin-bottom: 2rem;
  font-size: 1rem;
  font-weight: 600;
}

h6,
.h6 {
  margin-bottom: 2rem;
  font-size: 1rem;
}

.section-title {
  margin-bottom: 3.75rem;
}

.title-icon {
  margin-left: -6px;
}

.title-icon::before {
  content: '';
  position: relative;
  display: inline-block;
  width: 22px;
  height: 62px;
  background: url(../img/logo-icon.svg) 50% 50% no-repeat;
  background-size: contain;
}

.lead {
  display: block;
  font-size: 1.125rem;
  text-transform: none;
}

ul:not([class]),
ol:not([class]) {
  display: block;
  margin-left: 55px;
}

.btn-primary {
  font-weight: 700;
  letter-spacing: 1px;
  border: transparent;
  background-image: linear-gradient(225deg, #0a4644 0%, #218380 100%);
  transition: 3s;
}

.btn-primary:hover {
  background-image: linear-gradient(225deg, #681232 0%, #d81159 100%);
}

input.custom-control-input ~ label {
  margin-left: 26px;
}

input.custom-control-input ~ label::before {
  content: '';
  position: absolute;
  top: 4px;
  left: 1.25rem;
  display: inline-block;
  width: 16px;
  height: 16px;
  background-color: #f8ffe5ff;
}

input.custom-control-input ~ label::after {
  content: '';
  position: absolute;
  top: 6px;
  left: calc(1.25rem + 2px);
  display: inline-block;
  width: 10px;
  height: 10px;
}

input.custom-control-input[type=radio] ~ label::before,
input.custom-control-input[type=radio] ~ label::after {
  border-radius: 50%;
}

input.custom-control-input:checked ~ label::after {
  background-color: #D81159;
}

input.custom-control-input:disabled ~ label {
  color: #ced4da;
}

.full-w,
.full-h {
  position: relative;
  overflow: hidden;
}

.full-w.bordered,
.full-h.bordered {
  border: 18px solid #030303;
  border-bottom: none;
  border-radius: 8px;
  background-color: #030303;
}

.full-w {
  width: 100%;
}

.full-w img {
  display: inline-block;
  width: 100%;
  height: auto;
}

.full-h {
  height: 100%;
}

.full-h img {
  display: inline-block;
  width: auto;
  height: 100%;
}

.right-side .full-w,
.right-side .full-h,
.left-side .full-w,
.left-side .full-h {
  height: 500px;
}

.right-side .full-w img,
.right-side .full-h img,
.left-side .full-w img,
.left-side .full-h img {
  position: absolute;
  top: 0;
  left: 0;
}

.right-side .full-w img,
.right-side .full-h img {
  left: auto;
  right: 0;
}

.logo svg {
  width: auto;
  height: 60px;
}

.logo svg path.logo-text {
  fill: #000;
}

.index .header .logo svg path.logo-text {
  fill: #fff;
}

.str1 {
  stroke: black;
  stroke-width: 2.11;
  stroke-miterlimit: 2.61313;
}

.str0 {
  stroke: black;
  stroke-width: 2.2;
  stroke-miterlimit: 2.61313;
}

.fil1 {
  fill: none;
}

.fil0 {
  fill: #218380;
  fill-rule: nonzero;
}

.menu-trigger {
  padding-left: 60px;
  width: 100px;
  height: 30px;
  line-height: 30px;
  position: relative;
  color: red;
  text-transform: uppercase;
  transition: background 0.3s, color 0.5s;
  cursor: pointer;
  overflow: hidden;
}

.menu-trigger:focus {
  outline: none;
}

.menu-trigger span {
  display: block;
  width: 40px;
  height: 1px;
  position: absolute;
  top: 14px;
  left: 0;
  background: red;
  transition: background 0s 0.3s;
}

.menu-trigger span::before,
.menu-trigger span::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  background-color: #000;
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3s, 0s;
}

.menu-trigger span::before {
  top: -14px;
  transition-property: top, \transform;
}

.menu-trigger span::after {
  bottom: -15px;
  transition-property: bottom, \transform;
}

.navbar {
  flex-wrap: nowrap;
  padding: 0;
}

nav.navbar-collapse {
  display: flex;
  justify-content: flex-end;
  transition: .4s;
}

nav.navbar-collapse .navbar-nav {
  flex-direction: row;
  justify-content: flex-end;
  margin: 0 4%;
  width: 80%;
}

nav .nav-item {
  margin: 0 2%;
}

nav .nav-link {
  color: #232323;
  cursor: pointer;
}

nav .nav-link-toggle::after {
  content: "\f107";
  display: inline-block;
  font-family: "Font Awesome 5 Free";
  font-size: 65%;
  font-weight: 900;
  margin-left: .5rem;
  transition: 0.3s ease;
}

nav .navbar-collapse {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #333;
  z-index: 0;
}

nav .navbar-collapse .navbar-nav {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

a.target-burger {
  display: none;
  margin-left: 1.875rem;
  width: 61px;
  height: 30px;
  position: relative;
  transition: .5s;
  z-index: 10;
}

a.target-burger:hover {
  cursor: pointer;
}

a.target-burger.toggled ul.buns li.bun {
  background-color: #218380;
  transform: rotate(45deg) translateZ(0);
}

a.target-burger.toggled ul.buns li.bun:last-child {
  transform: rotate(-45deg) translateZ(0);
}

a.target-burger ul.buns {
  width: 31px;
  height: 31px;
  list-style: none;
  margin-bottom: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  left: 0;
  transition: transform 1s cubic-bezier(0.23, 1, 0.32, 1), color 1s cubic-bezier(0.23, 1, 0.32, 1);
  transform: translateZ(0);
  color: #fff;
}

a.target-burger ul.buns li.bun {
  width: 100%;
  height: 2px;
  background-color: #000;
  margin-top: -.75px;
  transform: translateY(-3.75px) translateZ(0);
  transition: transform 1s cubic-bezier(0.23, 1, 0.32, 1), background-color 1s cubic-bezier(0.23, 1, 0.32, 1);
}

a.target-burger ul.buns li.bun:last-child {
  transform: translateY(3.75px) translateZ(0);
}

.logo-icon {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 40px;
  height: 134px;
}

.logo-icon-block {
  position: relative;
  width: 134px;
  height: 40px;
}

.logo-icon svg {
  height: 100%;
}

.logo-icon.left {
  width: 134px;
  height: 40px;
}

.header {
  padding: 20px 0;
  width: 100%;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.02);
  z-index: 200;
}

.header {
  width: 100%;
}

.header a {
  font-family: "Source Code Pro", monospace;
  color: #218380;
  font-weight: 200;
  text-transform: uppercase;
  font-size: 1.5rem;
}

section {
  padding: 90px 0;
}

.header + section:not(.top):not(.jumbotron),
section.breadcrumb-nav + section,
section.breadcrumb-nav,
section.main {
  padding-top: 25px;
}

.header + section.jumbotron {
  padding-bottom: 0;
}

section.breadcrumb-nav {
  padding-bottom: 0;
}

.top {
  position: relative;
  height: 50vh;
  background: #000 url(../img/city.png) 50% 50% no-repeat;
  background-size: cover;
  overflow: hidden;
}

.index .top {
  padding-top: 40px;
}

.top .area-of-points {
  position: absolute;
  left: 0;
  bottom: 0;
  margin: 4%;
  width: 92%;
  height: 32%;
}

.top .area-of-points .point {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  flex-direction: column;
  width: 70px;
}

.top .area-of-points .point .icon {
  width: 100%;
  text-align: center;
}

.top .area-of-points .point .icon img {
  width: 75%;
}

.top .area-of-points .point .rating {
  position: absolute;
  right: -5px;
  bottom: 0;
  color: #fff;
  text-align: right;
}

.top .area-of-points .point .stars {
  position: absolute;
  right: 0;
  bottom: -20px;
  width: 70px;
  height: 27px;
  background: url(../img/stars-0.png) 50% 50% no-repeat;
  background-size: contain;
}

.top .area-of-points .point .stars.stars-1 {
  background-image: url(../img/stars-1.png);
}

.top .area-of-points .point .stars.stars-2 {
  background-image: url(../img/stars-2.png);
}

.jumbotron {
  position: relative;
  color: #fff;
  overflow: hidden;
}

.jumbotron * {
  color: #fff;
}

.jumbotron.red {
  background: #D81159;
  background: radial-gradient(circle, #d81159 0%, #681232 100%);
}

.jumbotron canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.video iframe {
  width: 100%;
  min-height: 500px;
}

.contacts {
  padding-bottom: 0;
}

.contacts .contact {
  font-size: 2rem;
  background-image: url(../img/pattern.png), linear-gradient(225deg, #218380 0%, #0a4644 100%);
  background-position: 0 4%;
  background-repeat: repeat;
}

.contacts .contact * {
  color: #fff;
}

.contacts .svg-map path {
  fill: #fff;
}

.contacts .team {
  background-color: #f8f9fa;
  border-top: 1px solid #dae0e5;
  border-bottom: 1px solid #dae0e5;
}

.contacts .team ul {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-left: 0;
}

.contacts .team ul li {
  display: block;
  margin: 10px;
  width: 33.3%;
  text-align: center;
  color: #218380;
  font-size: 1rem;
  font-weight: 400;
  text-transform: uppercase;
}

.contacts .team ul li img.photo {
  display: inline-block;
  width: 80%;
  max-width: 190px;
  margin: 20px;
}

.contacts .team ul li:first-child img {
  width: 84%;
  max-width: 200px;
}

.contacts .team ul li .cont {
  width: 100%;
}

.contacts .team ul li span {
  font-size: .8em;
  color: #232323;
}

/* ======== */

.triangles {
  position: relative;
  overflow: hidden;
}

.triangles.red svg .fil12 {
  fill: #D81159;
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.triangles.red svg .el-light {
  fill: rgba(216, 17, 89, 0.27);
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.triangles figure {
  position: absolute;
  top: 0;
  width: 40%;
  height: 100%;
  opacity: .8;
}

.triangles figure svg {
  width: auto;
  height: 100%;
}

.red .triangles figure svg .fil12 {
  fill: #D81159;
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.red .triangles figure svg .el-light {
  fill: rgba(216, 17, 89, 0.27);
  fill-rule: nonzero;
  fill-opacity: 0.4;
}

.triangles figure.triangle_l {
  left: 0;
}

.triangles figure.triangle_r {
  right: 0;
  transform: rotate(180deg);
}

.footer {
  padding: 20px 0;
  width: 100%;
  background-color: #f8f9fa;
  z-index: 200;
}

.footer p {
  font-size: .8em;
  font-weight: 100;
  text-align: center;
}

@media (min-width: 768px) {
  ::-webkit-scrollbar {
    width: 6px;
  }

  ::-webkit-scrollbar-track {
    background: rgba(33, 131, 128, 0.5);
  }

  ::-webkit-scrollbar-thumb {
    background: #218380;
  }

  ::-moz-selection {
    background: rgba(33, 131, 128, 0.8);
    color: #fff;
  }

  ::selection {
    background: rgba(33, 131, 128, 0.8);
    color: #fff;
  }
}

@media (max-width: 1400px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 1020px) {
  .logo-icon-block {
    display: none;
  }

  .header a {
    font-size: 18px;
  }
}

@media (max-width: 860px) {
  .triangles figure.triangle_l {
    left: -180px;
  }

  .triangles figure.triangle_r {
    right: -180px;
  }
}

@media (max-width: 770px) {
  header nav.navbar-collapse {
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 40%;
    height: 100vh;
    padding: 100px 60px;
    background-color: #f8f9fa;
    z-index: 2;
    transform: translateX(100%);
  }

  header nav.navbar-collapse .navbar-nav {
    flex-direction: column;
  }

  header nav.navbar-collapse .navbar-nav .nav-link {
    text-align: right;
  }

  header nav.navbar-collapse.toggled {
    transform: translateX(0);
  }

  a.target-burger {
    display: block;
  }
}

@media (max-width: 769px) {
  html {
    font-size: 14px;
  }

  body {
    font-size: 1rem;
  }

  section,
  .rise {
    padding: 40px 0;
  }

  h1,
  .h1 {
    font-size: 2.6rem;
  }
}

@media (max-width: 768px) {
  .columns .columns-block:not(:last-child) {
    margin-bottom: 3rem;
    border-bottom: 1px solid #d8d8d8;
  }
}

@media (max-width: 767px), (max-height: 760px) {
  .top {
    padding-top: 120px;
    padding-bottom: 0;
  }
}

@media only screen and (max-width: 767px) {
  .contacts .team ul li img.photo {
    margin: 20px 6px;
  }
}

@media (max-width: 600px) {
  .index .top {
    height: 90vh;
    padding-top: 10px;
  }

  .index .top .area-of-points {
    height: 42%;
  }
}

@media (max-width: 500px) {
  .header {
    padding: 8px 0;
  }

  .header .logo img {
    height: 40px;
  }
}

@media (max-width: 500px), (orientation: landscape) and (max-height: 400px) {
  html {
    font-size: 12px;
  }

  h1,
  .h1 {
    margin-bottom: 2.6rem;
    font-size: 2.3rem;
  }
}

@media (max-width: 460px) {
  .logo svg {
    height: 30px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .contacts .team ul {
    flex-direction: column;
  }

  .contacts .team ul li {
    display: flex;
    align-items: center;
    width: 100%;
    font-size: 0.875rem;
  }

  .contacts .team ul li img.photo {
    width: 50%;
    max-width: 140px;
  }
}

@media (orientation: landscape) and (max-height: 670px) {
  .top {
    height: auto;
  }
}